.videoshow {
  padding-top: 15px;
  flex-grow: 0;
  flex-basis: 345px;
  background-color: rgba(251, 136, 2, 0.96); }
  .videoshow lfx-player {
    width: 100%; }
    .videoshow lfx-player video {
      width: 100%; }
  .videoshow .video-info {
    margin-top: 15px; }

.lfx-player__controlbar {
  display: flex;
  width: 150px;
  height: 40px; }
.lfx-player__volume {
  display: flex;
  align-items: center;
  position: absolute;
  top: 50%;
  left: 20px;
  transform: translateY(-50%); }
  .lfx-player__volume paper-slider {
    width: 120px; }
    .lfx-player__volume paper-slider .expand:not(.pin) > #sliderKnob.paper-slider > #sliderKnobInner.paper-slider {
      width: 20px;
      height: 20px; }
.lfx-player__toggle {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%); }
.lfx-player__list {
  position: absolute;
  top: 50%;
  right: 45px;
  transform: translateY(-50%); }
.lfx-player__full {
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translateY(-50%); }

.paignation paper-button {
  width: 55px;
  min-width: 0;
  padding-left: 0;
  padding-right: 0; }
  .paignation paper-button a {
    text-decoration: none; }
  .paignation paper-button:hover {
    background-color: #51a8dd; }
    .paignation paper-button:hover a {
      color: #ffffff; }


